import Vue from 'vue';
import { Card, Button } from 'element-ui';
import PageHeaderLayout from 'layouts/PageHeaderLayout/index.vue';
import Result from 'components/Result';
import AntIcon from 'components/AntIcon';
Vue.use(Card);
Vue.use(Button);
Vue.use(Result);
Vue.use(AntIcon);
export default Vue.extend({
    components: {
        PageHeaderLayout
    }
});



